.nav {
    grid-area: n;
    background-color: #232c40;
    height: 600px;
    color: whitesmoke;
    padding-left: 40px;
    padding-right: 40px;
    padding-top: 30px;
    border-radius: 30px;
}

.navItem {
    padding-top: 10px;
    font-size: 18px;
}

.navItem a {
    text-decoration: none;
    color: whitesmoke;
}

.navItem a.active {
    background-color: #465065;

}
