}

.Preloader_loadingText__v4XXr {
    font-family: 'Raleway', sans-serif;
    font-weight: bold;
    font-size: 2em;
    position: absolute;
    top: 50%;
    left: 50%;
    margin-right: -50%;
    transform: translate(-50%, -50%);
}

@keyframes Preloader_borderScale__1t5IZ {
    0% {
        border: 5px solid white;
